<template>
  <div class='com-container'>
    <div class='com-chart' ref='wordcloud_ref'></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      allData: null // 从服务器中获取的所有数据
    }
  },
  mounted () {
    this.initChart()
    this.getData()
    window.addEventListener('resize', this.screenAdapter)
    this.screenAdapter()
  },
  props: {
    'startAndEndArrToWordCloud':{
      start: String,
      end: String
    }
  },
  watch: {
    startAndEndArrToWordCloud(newStartAndEndArr){
      console.log("WordCloud has got startAndEndArr from Testpage, which is " + newStartAndEndArr['start'] + " to " + newStartAndEndArr['end'])
      this.makeWordCloud(newStartAndEndArr['start'], newStartAndEndArr['end'])
    }
  },
  destroyed () {
    window.removeEventListener('resize', this.screenAdapter)
  },
  methods: {
    async initChart () {
      this.chartInstance = this.$echarts.init(this.$refs.wordcloud_ref)
      this.makeWordCloud('2009-05', '2020-01')
      // maskImage.src = '../assets/wordcloud.png'
    },
    async makeWordCloud(startTime, endTime){
          // const { data: keywords } = await this.$http.get('wordcloud')
          const { data: keywords } = await this.$http({
              method: 'post',
              url: '/help/wordcloud',
              params: {
                  startTime: startTime,
                  endTime: endTime
              }
          })
          console.log(keywords)
          const data = keywords.map((item) => {
              return {
                  name: item.name,
                  value: Math.sqrt(item.value)
              }
          })
          // console.log(data)
          // const data = []
          // for (const name in keywords) {
          //   data.push({
          //     name: name,
          //     value: Math.sqrt(keywords[name])
          //   })
          // }
          const maskImage = new Image()
          maskImage.src =
              'data:image/png;base64,'
          // const initOption = {}
          // this.chartInstance.setOption(initOption)
          maskImage.onload = () => {
              // initOption.series[0].maskImage
              this.chartInstance.setOption({
                  series: [
                      {
                          type: 'wordCloud',
                          sizeRange: [10, 40],
                          rotationRange: [-90, 90],
                          rotationStep: 45,
                          gridSize: 2,
                          shape: 'pentagon',
                          maskImage: maskImage,
                          drawOutOfBound: false,
                          textStyle: {
                              normal: {
                                  color: function () {
                                      return (
                                          'rgb(' +
                                          [
                                              Math.round(Math.random() * 160),
                                              Math.round(Math.random() * 160),
                                              Math.round(Math.random() * 160)
                                          ].join(',') +
                                          ')'
                                      )
                                  }
                              },
                              emphasis: {
                                  color: 'red'
                              }
                          },
                          data: data.sort(function (a, b) {
                              return b.value - a.value
                          })
                      }
                  ]
              })
          }
      },
    async getData () {
      // await this.$http.get()
      // 对allData进行赋值
      this.updateChart()
    },
    updateChart () {
      const dataOption = {}
      this.chartInstance.setOption(dataOption)
    },
    screenAdapter () {
      const adapterOption = {}
      this.chartInstance.setOption(adapterOption)
      this.chartInstance.resize()
    }
  }
}
</script>
<style lang='less' scoped>
</style>
